.wrapper {
    display: flex;
    justify-content: space-between;
    margin-top: 10vh;
}

.container {
    box-sizing: border-box;
    padding: 25px;
    width: 45%;
    height: 300px;
    background: var(--translation-fields-background);
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.25);
    border-radius: 20px;
}

.language_header {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 36px;
    color: var(--language-header-font-color);
    cursor: default;
}

.arrows{
    display: flex;
    flex-direction: column;
    justify-content: center;
    max-width: 10%;
    min-width: 52px;
}

.arrow{
    height: 52px;
    width: 52px;
    background-image: url(../../assets/arrow.svg);
}

.arrow:last-child{
    rotate: 180deg;
}