.container {
    box-sizing: border-box;
    background-color: var(--switcher-background);
    height: 40px;
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 180px;
    border-radius: 12px;
    padding: 2.5px;
    margin-top: 10vh;
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.25);
}

.theme {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 16px;
    color: var(--color-text-main);
    border: 2px solid transparent;
    padding: 8px 12px;
    border-radius: 8px;
    display: flex;
}

[data-theme="dark"] .dark {
    color: var(--logo-font-color);
    border: 1px solid var(--switcher-outline);
    background-color: var(--switcher-chosen-dark);
    transition: var(--change-theme-duration);
}

[data-theme="light"] .light {
    color: var(--logo-font-color);
    border: 1px solid var(--switcher-outline);
    background-color: var(--switcher-chosen-light);
    transition: var(--change-theme-duration);
}

.icon{
    width: 16px;
    height: 16px;
    margin-right: 3px;
}

.icon_light{
    background-image: url(../../assets/light.svg);
}

.icon_dark{
    background-image: url(../../assets/dark.svg);
}